import React from 'react';
import classes from './details.module.css';
import {Link} from 'react-router-dom';

class Details extends React.Component {
    constructor(props) {
        super(props);
        this.state ={
            data: this.props.location.state,
        }
        console.log("Details", props);
    }
    addtoCart(){
        alert('Congrats! Your item has been added');
    }
    render() {
        return(
            <div className={classes.details_holder}>
                <img src={this.state.data.image} alt="" />
                {/* <div className={classes.product_img} style={{backgroundImage: `url(${this.state.data.image})`}}></div> */}
                <div className={classes.details_content}>
                    <h1>{this.state.data.name}</h1>
                    <p>RM {this.state.data.cost}</p>
                    <h2>{this.state.data.short_description}</h2>
                    <ul>
                        {this.state.data.bullet_description.map((bullet) => (
                            <li>{bullet}</li>
                        ))}
                    </ul>
                    <button className={classes.btn} onClick={() => {this.addtoCart()}}>
                        <Link className={classes.link} to="/dashboard">Add to cart</Link>
                    </button>
                </div>
            </div>
        );
    }
}
export default Details;