import React from 'react';
import {products} from "../../productData";
import classes from './createProduct.module.css';

class CreateProduct extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            id: '',
            name: '',
            desc: '',
            cost: '',
        }
    }
    submitForm() {
        console.log(this.state);
        console.log(products);
        if(this.state.id === "" || this.state.name === "" || this.state.desc === "" || this.state.cost === "") {
            alert("Please fill in all the details");
        } else {
            var productId = products.find(element => element["id"] === this.state.id);
            if (productId === undefined) {
                alert("Your product has been added into the list!");
                products.push(this.state);
            } else {
                alert('This Id has been taken. Please insert another Id');
            }
        }
    }
    render() {
        return(
            <form action="#" className={classes.holder}>
                <div className={classes.search_holder}>
                    <div className={classes.new_product}>
                        <label htmlFor="id">ID</label>
                        <input id="id" type="text" placeholder="ID" onChange={(idvalue) => {
                            this.setState({id: idvalue.target.value});
                            // console.log(idvalue.target.value);
                        }} />
                    </div>
                    <div className={classes.new_product}>
                        <label htmlFor="name">Name</label>
                        <input id="name" type="text" placeholder="Name" onChange={(nameval) => {
                            this.setState({name: nameval.target.value});
                        }} />
                    </div>
                    <div className={classes.new_product}>
                        <label htmlFor="desc">Description</label>
                        <input id="desc" type="text" placeholder="Description" onChange={(desc) => {
                            this.setState({desc: desc.target.value});
                        }} />
                    </div>
                    <div className={classes.new_product}>
                        <label htmlFor="cost">Cost</label>
                        <input id="cost" type="text" placeholder="Cost" onChange={(costValue) => {
                            this.setState({cost: costValue.target.value});
                        }} />
                    </div>
                </div>             
                <button className={classes.btn} onClick={() => {this.submitForm()}}>Submit</button>
            </form>
        );
    }
}
export default CreateProduct;