import React from 'react';
import {products} from '../../productData';
import Content from '../../components/content';
import classes from './dashboard.module.css';

import {Link} from 'react-router-dom';

class Dashboard extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            name: '',
        }
    }
    render() {
        console.log('Products data...', products);
        return(
            <div>
                <div className={classes.dashboard_header}>
                    <div >
                        <input className={classes.search} type="text" placeholder="Search" onChange={(name) => {
                            this.setState({name: name.target.value});
                        }} />
                        <button className={classes.btn}>
                        <Link to="/create" className="link">+</Link>
                        </button>
                    </div>
                    <button className={classes.btn}>
                        <Link to="/" className="link">Log out</Link>
                    </button>
                </div>
                <div>
                    {products.filter((list) => {
                        if (this.state.name === ""){
                            return list;
                        } else if (list.name.toLowerCase().includes(this.state.name.toLowerCase())){
                            return list;
                        }
                    }).map(list => 
                    <Content carData={list} link={list.id} name={list.name} image={list.image} cost={list.cost} short_description={list.short_description} />
                    )}
                </div>
            </div>
        );
    }
}
export default Dashboard;