import React from 'react';
import classes from './login.module.css'; 

import {Link} from 'react-router-dom';

class Login extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            email: '',
            pw: '',
        }
    }
    // login() {
    //     if(this.state.email === "" | this.state.pw === "") {
    //         alert('You must fill in your credentials!');
    //     } else {
    //         <Link to="/dashboard">Login</Link>
    //     }
    // }
    render() {
        return(
            <div className={classes.login}>
                <h1 style={{color:`#154c79`}}>Welcome to Kereta.</h1>
                <p>Please fill in the details to view the dashboard..</p>
                <input className={classes.input} type="text" id="email" placeholder="email" onChange={email => this.setState({email: email.target.value})} />
                <input className={classes.input} type="text" id="password" placeholder="password" onChange={password => this.setState({pw: password.target.value})} />
                {/* onClick={() => {this.login()}} */}
                <button className={classes.btn}>
                    <Link className="link" to="/dashboard">Login</Link>
                </button>
            </div>
        );
    }
}
export default Login;