import React from 'react';
import classes from './content.module.css';
import {Link} from 'react-router-dom';

class Content extends React.Component {
    render() {
        return(
            <div className={classes.content_holder}>
                <div className={classes.img} style={{backgroundImage: `url(${this.props.image})`}}></div>
                <div className={classes.content}>
                    <h2 style={{color: `rgb(156, 156, 6)`}}>{this.props.name}</h2>
                    <h3 style={{color: `rgb(85, 85, 85)`}}>{this.props.short_description}</h3> 
                    <p>{this.props.bullet_description}</p>
                    <p>RM {this.props.cost} </p>
                    <button className={classes.btn}>
                        {/* <Link to={`/dashboard/${this.props.link}`}> {this.props.cost} </Link> */}
                        <Link className={classes.link} to={{
                            pathname:`/dashboard/${this.props.link}`,
                            state: this.props.carData, //pass the carData object
                        }}> 
                        Details
                        </Link>
                    </button>
                </div>
            </div>
        );

    }
}
export default Content;